import { Link } from "react-router-dom";
import { useState } from "react";

export default function Card(props) {
  const [isActive, setIsActive] = useState(false);
  const cardText = props.cardText;

  function handleClickCard() {
    if (props.onClick) {
      props.onClick(isActive, setIsActive);
    }
  }

  return (
    <>
      <Link
        to={props.cardLink}
        className={
          props.cardClassName
            ? isActive
              ? "card card_active " + props.cardClassName
              : "card " + props.cardClassName
            : "card"
        }
        onClick={handleClickCard}
      >
        <div
          className={
            props.titleItemClass ? props.titleItemClass : "card__title-item"
          }
        >
          {props.children}
        </div>
          {cardText ? (
            cardText.map((text, i) => (
              <p
                className={
                  isActive
                    ? `${props.className} + card__description_active`
                    : props.className
                }
                key={i}
              >
                {text}
              </p>
            ))
          ) : (
            <p></p>
          )}
      </Link>
    </>
  );
}
